.world-map {
	width: 100%;
	background: url('../images/map.jpg') no-repeat center center;
}

.world-map td {
	width: 100px;
	height: 100px;
	border: 1px dashed rgba(0,0,0, 0.2);
	text-align: center;
}

.world-castle {
	width: 80px;
	display: inline-block;
	margin: 0px auto;
}